* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body { 
  padding: 60px;;
 }

 input {
   color: grey;
   font-size: 1.2rem;
   width: 100%;
 }

.container {
  width: 100%;
  max-width: 1200px;
  height: 430px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
}

.container .card {
  width: 330px;
  height: 430;
  border-radius: 8px;
  box-shadow: 0 2px rgba(0,0,0, 0.2);
  overflow: hidden;
  margin: 20px;
  text-align: center;
  transition: all 0.25;
  padding-bottom: 50px;
}

.container .card:hover{
  transform: translateY(-15px);
  box-shadow: 0 12px 16px rgba(0,0,0, 0.2);
}

.container .card img {
  width: 330px;
  height: 220px;
}